/*------------------------------------*\
    ::Timeline
\*------------------------------------*/

/*------------------------------------*\
    ::Common Vars
\*------------------------------------*/
$timeline-width:            3em; // timeline bar's width
$timeline-gaps:             7em; // each year period's height
$timeline-border-width:     3px; // all timeline borders
$year-word-height:          .85em; // the height of the "20xx" word for centering

/*------------------------------------*\
    ::Year Position Mixin
\*------------------------------------*/
// Usage:
// .el {
//     @include year(2010, 2015); // spans 2010 to 2015
//     @include year(false, 2015); // starts at 2015 with auto height
//     @include year(false, 2015.5); // starts at Jul 2, 2015 with auto height
// }
@mixin year($start-year, $end-year, $total-years: 2016) {
    top: ($total-years - $end-year + 1) * $timeline-gaps + $year-word-height;
    @if $start-year != false {
        height: ($end-year - $start-year) * $timeline-gaps;
    }
}
.timeline {
    @include clearfix;
    position: relative;
    margin-top: 100vh;
    float: left;
    width: 100%;
    &__col {
        float: left;
        width: 33.33%;
        padding: 0 $gap;
        @include bp($solarsystem){
            width: 50%;
        }
        @include bp($planetary){
            width: 100%;
        }
    }
}
// .detail { // left col

// }
.exp { // center col
    @include clearfix();
    position: relative;
    padding-right: 0;
    h1, h2, h3, h5, h6 {
        color: $color-1-dark;
    }
    &__group {
        position: absolute;
        left: 0;
        right: $timeline-width;
        padding: 0 $gap;
         // /* DEBUG */box-shadow:0 0 0 1px rgba(tomato,.5) inset;background-color:rgba(tomato,.5);
        &--title {
            &:before,
            &:after {
                display: none;
            }
        }
        &--zenman {
            @include year(2012, 2016);
        }
        &--du {
            @include year(2010, 2012);
        }
        &--usmc {
            @include year(2006, 2010);
        }
        &--tech {
            @include year(2002, 2006);
        }
        &:before,
        &:after {
            content: "";
            position: absolute;
            border: $timeline-border-width solid $color-2;
            z-index: 10;
        }
        &:before {
            left: 0;
            right: 0;
            bottom: 0;
            border-width: $timeline-border-width 0 0;
        }
        &:after {
            right: -$timeline-border-width*.5;
            bottom: $timeline-border-width*.4;
            width: 1em;
            height: 1em;
            border-radius: 50%;
            background-color: $color-bg--light;
            transform: translate(50%, 50%);
        }
    }
    &__timeline {
        float: right;
        width: $timeline-width;
        border: 0 solid $color-2;
        border-width: 0 $timeline-border-width;
        margin: 0;
        padding: 0;
    }
    &__year {
         // /*TEMP*/box-shadow:0 0 0 1px rgba(purple,.5) inset;background-color:rgba(purple,.5);
        position: relative;
        display: block;
        text-align: center;
        width: 100%;
        height: $timeline-gaps;
        padding-left: 0;
        span {
            font-size: .75em;
        }
        &:before,
        &:after {
            content: "";
            position: absolute;
            border: 0 solid $color-2;
            border-width: $timeline-border-width 0 0;
            width: .4em;
            height: 0;
            top: .7em;
            border-radius: 0;
        }
        &:before {
            left: 0;
        }
        &:after {
            right: 0;
        }
    }
}
.achieve { // right col
    position: absolute;
    right: 0;
    top: 0;
    bottom: 0;
    @include bp($solarsystem){
        position: relative;
        width: 100%;
    }
    &__group {
        position: absolute;
        left: 0;
        right: 0;
        padding: 0 $gap 0 3em;
        transform: translateY(-50%);
        &--senior {
            @include year(false, 2012);
        }
        &--lead {

        }
        &--goodcookie {

        }
        &--usmc {

        }
        &--tech {

        }
        &:before,
        &:after {
            content: "";
            position: absolute;
            border: $timeline-border-width solid $color-2;
            z-index: 10;
        }
        &:before {
            left: 0;
            width: 2em;
            top: 50%;
            border-width: $timeline-border-width 0 0;
            transform: translateY(-100%);
        }
        &:after {
            left: -.1em;
            top: calc(50% - .1em);
            width: 1em;
            height: 1em;
            border-radius: 50%;
            background-color: $color-bg--light;
            transform: translate(-50%, -50%);
        }
    }
}
